<template>
  <div>
    <gs-button
      type="primary"
      @click="openAddMemberModal"
    >
      添加成员
    </gs-button>
    <gs-button
      type="primary"
      @click="removeMember"
    >
      移除成员
    </gs-button>
    <gs-modal
      v-model="visible"
      title="添加成员"
      width="680px"
      @visible-change="visibleChange"
      @confirm="addMember"
    >
      <div class="add-member-wapper">
        <div class="member-search-list">
          <div class="header">
            选项
          </div>
          <gs-search
            v-model="keyword"
            class="search"
            @search="handleSearch"
            @clear="handleSearch"
          >
            <template slot="prepend">
              <gs-select
                v-model="roleFilter"
                @change="roleChange"
              >
                <gs-option
                  v-for="item in roleOptions"
                  :key="item.roleId"
                  :label="item.roleName"
                  :value="item.roleId"
                  :title="item.roleName"
                />
              </gs-select>
            </template>
          </gs-search>
          <ul class="search-list">
            <li
              v-for="(item,index) in memberOptions"
              :key="index"
              :style="item.isActive ? 'color:#0073e8;':''"
              class="list-item"
              @click="doSelectItem(item,index)"
            >
              <span
                :title="item.username"
                class="label"
              >{{ item.username }}
              </span>
              <span
                v-if="item.roleName"
                :title="item.roleName"
                class="account"
              >
                ({{ item.roleName }})
              </span>
            </li>
          </ul>
        </div>
        <div class="member-select-list">
          <div class="header">
            <span class="header-left">
              已选择
            </span>
            <span class="header-right">
              已选中 {{ selectItemList.length }} 项
            </span>
          </div>
          <ul class="selected-list">
            <li
              v-for="(item,index) in selectItemList"
              :key="index"
              class="list-item"
            >
              <span
                :title="item.username"
                class="label"
              >{{ item.username }}
              </span>
              <span
                v-if="item.account"
                :title="item.account"
                class="account"
              >
                ({{ item.account }})
              </span>
              <i
                class="gs-icon-close"
                @click="unselectOption(item)"
              />
            </li>
          </ul>
        </div>
      </div>

      <gs-loading
        :is-show="loading"
      />
    </gs-modal>
  </div>
</template>
<script>
import * as roleService from 'src/service/auth/role';
export default {
  props: {
    roleId: {
      type: String,
      required: true,
      default: ''
    }
  },
  data() {
    return {
      visible: false,
      roleOptions: [],
      roleFilter: 'all',
      memberOptions: [],
      keyword: '',
      selected: [],
      selectItemList: [],
      loading: false
    };
  },
  watch: {
    memberOptions(nval) {
      nval.forEach(e => {
        if (this.selected.includes(e.userId)) {
          e.isActive = true;
        }
      });
    }
  },
  methods: {
    openAddMemberModal() {
      this.visible = true;
    },
    doSelectItem(item, index) {
      this.memberOptions[index]['isActive'] = !this.memberOptions[index]['isActive'];
      if (this.memberOptions[index]['isActive']) {
        this.selected.push(item.userId);
        this.selectItemList.push(item);
      } else {
        const index = this.selected.findIndex(e => e === item.userId);
        this.selected.splice(index, 1);
        this.selectItemList.splice(index, 1);
      }
    },
    removeMember() {
      this.$emit('removeMember');
    },
    visibleChange(val) {
      if (val) {
        this.loading = true;
        this.roleFilter = 'all';
        // this.selected = [];
        roleService.getRoleList({limit: -1, query: ''}).then(data => {
          data.lists = [
            {
              roleName: '全部',
              roleId: 'all'
            },
            ...data.lists
          ];
          const index = data.lists.findIndex(item => item.roleId === this.roleId);
          if (index !== -1) data.lists.splice(index, 1);
          this.roleOptions = data.lists.filter(i => i.roleId !== '1');
        });
        roleService.getAllMemberList(this.roleId, {limit: -1, query: this.keyword}).then(data => {
          this.memberOptions = data.lists;
          this.loading = false;
        });
      }
    },
    addMember() {
      if (this.selected && this.selected.length) {
        this.$emit('addMember', this.selected);
        this.visible = false;
      } else {
        this.$Message.warning(`请选择成员`);
      }
    },
    handleSearch() {
      if (this.roleFilter === 'all') {
        roleService.getAllMemberList(this.roleId, {limit: -1, query: this.keyword}).then(data => {
          this.memberOptions = data.lists;
        });
      } else {
        roleService.getMemberList(this.roleFilter, {limit: -1, query: this.keyword}).then(data => {
          this.memberOptions = data.lists;
        });
      }
    },
    roleChange() {
      this.keyword = '';
      if (this.roleFilter === 'all') {
        roleService.getAllMemberList(this.roleId, {limit: -1, query: ''}).then(data => {
          this.memberOptions = data.lists;
        });
      } else {
        roleService.getMemberList(this.roleFilter, {limit: -1, query: ''}).then(data => {
          this.memberOptions = data.lists;
        });
      }
    },
    rightSelectedRender(h, option) {
      const optionItem = this.memberOptions.find(i => option.value === i.userId);
      return (
        <div class="right-slected">
          <span
            class="label"
            title={optionItem.username}
          >{optionItem.username}
          </span>
          {optionItem.account
            ? <span
              class="account"
              title={optionItem.account}>
              ({optionItem.account})
            </span>
            : null}
          <i class="gs-icon-close" on-click={this.unselectOption.bind(this, option)} />
        </div>
      );
    },
    unselectOption(item) {
      const index = this.selected.findIndex(i => i === item.userId);
      this.selected.splice(index, 1);
      this.selectItemList.splice(index, 1);
      this.memberOptions.forEach(e => {
        if (e.userId === item.userId) {
          e.isActive = false;
        }
      });
    }
  }
};
</script>

